<template>
  <v-container>
    <div>
      <template>
        <v-data-table
          :headers="headers"
          :items="peliculas"
          :items-per-page="5"
          class="elevation-1"
        >
        </v-data-table>
      </template>
      
      <v-btn depressed color="primary" @click="listar()"> Traer datos </v-btn>
    </div>
  </v-container>
</template>

<script>
import axios from "axios";
export default {
  name: "PageInicio",

  data() {
    return {
      token: this.$store.state.token,
      datos: this.$store.state.datos,
      peliculas: [],
      headers: [
        { text: "Numero_cotizacion", value: "Numero_cotizacion" },
        { text: "Fecha_emision", value: "Fecha_emision" },
        { text: "Idcliente", value: "Idcliente" },
        { text: "Idcontacto", value: "Idcontacto" },
        { text: "Validez_oferta", value: "Validez_oferta" },
        { text: "Entrega_resultados", value: "Entrega_resultados" },
        { text: "Elabordo_por", value: "Elabordo_por" },
        
      ],
    };
  },
  methods: {
    listar() {
      let header = { headers: { "x-token": this.$store.state.token } };
      axios
        .get("http://localhost:4000/api/cotizacion", header)
        .then((response) => {
          console.log(response);
          this.peliculas = response.data.pelicula;
        })
        .catch((error) => {
          console.log(error);
        });
    },
  },
};
</script>
